import React, { Fragment } from 'react';
import {  HashRouter, Route, Switch  } from 'react-router-dom'
import Home from './pages/home/home'
import Login from './pages/login/login'
function App() {
  return (
    <div className="App">
     <Fragment>
      <HashRouter>
        <Switch>
          <Route path='/' key={1} component={Home}></Route>
          <Route path='/login' key={2} component={Login}></Route>
        </Switch>
      </HashRouter>
    </Fragment>
    </div>
  );
}

export default App;
