import { useEffect, useState } from "react";
import { Table, Card, Modal, Descriptions  } from 'antd';
import mockData from '../../assets/data/data'
import '../../assets/css/phone.css'
const { Column, ColumnGroup } = Table;

const data = mockData;
// console.log(data,'data')
export type Param = {
    name?:string
    price?:string
    url?:string
    processor?:string
    ram?:string
    rom?:string
    battery?:string
    power?:string
    frontCamera?:string
    rearCamera?:string
    size?:string
    screen?:string
    weight?:string
    nfc?:string
    G5?:string

}
  
function GetTable(){
    const [show, setShow] = useState(false);
    const [params, setParams] = useState<Param>({});
    const showModal = (v:any)=>{
        setShow(true)
        console.log(v)
        if(v){
            setParams(v)
        }
    }
    const closeModal = ()=>{
        setShow(false)
    }
      return(
          <div>
          <Table dataSource={data} pagination={false} scroll={{ x: 1500, y: 600 }}>
            <Column title="价格\品牌" dataIndex="grade" key="grade" fixed="left" width="100px" align="center"/>
            <Column title="小米" dataIndex="mi" key="mi" align="center" render={mi => (
                <>
                <Card hoverable>
               <ul className="products_list">
                    {
                        mi.map((item:any,index:number)=>{
                            return(
                                <li key={index} onClick={() => showModal(item.params)}>
                                    <img src={item.pic} alt="picture" className="table_img"/>
                                    <p>{item.name}</p>
                                    <p>{item.price}起</p>
                                </li>
                            )
                        })
                    }
                    {
                        mi.length <= 0 ? '暂无机型' : ''
                    }
                </ul>
                </Card>
                </>
            )}/>
            <Column title="华为" dataIndex="wei" key="wei" align="center" render={wei => (
                <>
                <Card hoverable>
               <ul className="products_list">
                    {
                        wei.map((item:any,index:number)=>{
                            return(
                                <li key={index} onClick={() => showModal(item.params)}>
                                    <img src={item.pic} alt="picture" className="table_img"/>
                                    <p>{item.name}</p>
                                    <p>{item.price}起</p>
                                </li>
                            )
                        })
                    }
                    {
                        wei.length <= 0 ? '暂无机型' : ''
                    }
                </ul>
                </Card>
                </>
            )}/>
            <Column title="苹果" dataIndex="apple" key="apple" align="center" render={apple => (
                <>
                <Card hoverable>
                <ul className="products_list">
                    {
                        apple.map((item:any,index:number)=>{
                            return(
                                <li key={index} onClick={() => showModal(item.params)}>
                                    <img src={item.pic} alt="picture" className="table_img"/>
                                    <p>{item.name}</p>
                                    <p>{item.price}起</p>
                                </li>
                            )
                        })
                    }
                    {
                        apple.length <= 0 ? '暂无机型' : ''
                    }
                </ul>
                </Card>
                </>
            )}/>
            <Column title="oppo" dataIndex="oppo" key="oppo" align="center" render={oppo => (
                <>
                <Card hoverable>
                <ul className="products_list">
                    {
                        oppo.map((item:any,index:number)=>{
                            return(
                                <li key={index} onClick={() => showModal(item.params)}>
                                    <img src={item.pic} alt="picture" className="table_img"/>
                                    <p>{item.name}</p>
                                    <p>{item.price}起</p>
                                </li>
                            )
                        })
                    }
                    {
                        oppo.length <= 0 ? '暂无机型' : ''
                    }
                </ul>
                </Card>
                </>
            )}/>
            <Column title="vivo" dataIndex="vivo" key="vivo" align="center" render={vivo => (
                <>
                <Card hoverable>
                <ul className="products_list">
                    {
                        vivo.map((item:any,index:number)=>{
                            return(
                                <li key={index} onClick={() => showModal(item.params)}>
                                    <img src={item.pic} alt="picture" className="table_img"/>
                                    <p>{item.name}</p>
                                    <p>{item.price}起</p>
                                </li>
                            )
                        })
                    }
                    {
                        vivo.length <= 0 ? '暂无机型' : ''
                    }
                </ul>
                </Card>
                </>
            )}/>
        </Table>
        <Modal
            title="手机参数"
            centered
            visible={show}
            width={1000}
            footer={false}
            onCancel={closeModal}
        >
            <Descriptions title="" column={1} bordered size="small">
                <Descriptions.Item label="型号">{params.name}</Descriptions.Item>
                <Descriptions.Item label="官网价格">{params.price}起</Descriptions.Item>
                <Descriptions.Item label="官网链接"><a href={params.url}>点击去官网购买</a></Descriptions.Item>
                <Descriptions.Item label="处理器">{params.processor}</Descriptions.Item>
                <Descriptions.Item label="运行内存">{params.ram}</Descriptions.Item>
                <Descriptions.Item label="机身储存">{params.rom}</Descriptions.Item>
                <Descriptions.Item label="充电功率">{params.power}</Descriptions.Item>
                <Descriptions.Item label="电池容量">{params.battery}</Descriptions.Item>
                <Descriptions.Item label="前置相机">{params.frontCamera}</Descriptions.Item>
                <Descriptions.Item label="后置相机">{params.rearCamera}</Descriptions.Item>
                <Descriptions.Item label="尺寸">{params.size}</Descriptions.Item>
                <Descriptions.Item label="重量">{params.weight}</Descriptions.Item>
                <Descriptions.Item label="NFC">{params.nfc === '1' ? '有':'无'}</Descriptions.Item>
                <Descriptions.Item label="5G">{params.G5 === '1' ? '有':'无'}</Descriptions.Item>
            </Descriptions>
        </Modal>
        </div>
    )
}
export default function Phone() {
    return (
        <div>
            <GetTable></GetTable>
        </div> 
    )
}